import Vue from 'vue';
import Router from 'vue-router';

import Login from './views/Login.vue';
import PrimeiroAcesso from './views/PrimeiroAcesso.vue';

import Pedidos from './views/Pedidos.vue';
import Cardapio from './views/Cardapio.vue';
import Resumo from './views/Resumo.vue';
import Configuracoes from './views/Configuracoes.vue';
import Perfil from './views/Perfil.vue';
import NovoFuncionario from './views/NovoFuncionario.vue';
import NovoPrato from './views/NovoPrato.vue';
import Ajuda from './views/Ajuda.vue';

Vue.use(Router);

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'bodybox',
      component: Login,
      meta: {
        requiresAuth: false,
      },
    },
    {
      path: '/login',
      name: 'login',
      component: Login,
      meta: {
        requiresAuth: false,
      },
    },
    {
      path: '/primeiro-acesso',
      name: 'primeiroAcesso',
      component: PrimeiroAcesso,
      meta: {
        requiresAuth: false,
      },
    },
    {
      path: '/pedidos',
      name: 'pedidos',
      component: Pedidos,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: '/cardapio',
      name: 'cardapio',
      component: Cardapio,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: '/novo-prato',
      name: 'novo-prato',
      component: NovoPrato,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: '/resumo',
      name: 'resumo',
      component: Resumo,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: '/configuracoes',
      name: 'Configuracoes',
      component: Configuracoes,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: '/novo-funcionario',
      name: 'novo-funcionario',
      component: NovoFuncionario,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: '/meu-perfil',
      name: 'meu-perfil',
      component: Perfil,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: '/funcionario/:id',
      name: 'funcionario-perfil',
      component: NovoFuncionario,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: '/ajuda',
      name: 'ajuda',
      component: Ajuda,
      meta: {
        requiresAuth: true,
      },
    },
  ],
});
